#navbar {
  position: fixed;
  padding: 15px;
  border: 1px solid rgba(34, 36, 38, 0.15);
  background: #fff;
  -webkit-transition: all 0.4s linear;
  -o-transition: all 0.4s linear;
  transition: all 0.4s linear; }
  #navbar .header {
    font-size: 2rem; }
  #navbar #logo:hover {
    background: #fff; }

.small {
  padding: 0 !important; }
  .small .header {
    font-size: 1.5rem !important; }

.menu .ui.input input {
  color: #fff; }

.menu .ui.input input:focus {
  color: #fff; }



